@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
html,
body,
#root {
  margin: 0;
  padding: 0;
}

body {
  background: url(/TheDragonPrinceMap/static/media/StarSky.3957547f.gif);

  color: #eee;
  text-shadow: 1px 1px 1px #111;

  font-family: "Poppins", sans-serif;
}

@media (max-aspect-ratio: 819 / 546) {
  html,
  body,
  #root {
    height: 100%;
    width: auto;
    overflow: hidden;
  }
  #root {
    overflow: scroll;
  }
}
.map-appear-enter #mapCircleMask {
    animation: show-map 2s ease-in-out 2s 1 normal forwards;
}
.map-appear-enter #mapStrokeCircleMask {
    animation: show-map-stroke-mask 4s ease-in-out 0s 1 normal forwards;
}
.map-appear-enter .Map-Region {
    animation: hide-map-stroke 4s ease-in-out 0s 1 normal forwards;
}

.map-appear-enter-done #mapCircleMask,
.map-appear-enter-done #mapStrokeCircleMask {
    animation: none;
    r: 600px;
}
.map-appear-enter-done #strokeImage image {
    -webkit-mask: none;
            mask: none;
}
.map-appear-enter-done .Map-Region {
    opacity: 0;
    fill: url(#mapRegionGradientBackground);
    transition: .2s ease-in-out;
    animation: none;
}
.map-appear-enter-done .Map-Region:hover {
    opacity: 1;
}

.Map-Region {
    fill: transparent;
    stroke: none;
    stroke-width: 2;
    opacity: 0;
    cursor: pointer;

    stroke: url(#strokeImage);
    -webkit-filter: url(#glow);
            filter: url(#glow);
}

@keyframes show-map {
    from {
        r: 0px;
    }
    to {
        r: 600px;
    }
}
@keyframes show-map-stroke-mask {
    0% {
        r: 0px;
        opacity: 1;
    }
    80% {
        r: 600px;
        opacity: 1;
    }
    100% {
        r: 600px;
        opacity: 0;
    }
}
@keyframes hide-map-stroke {
    0% {
        opacity: 1;
    }
    80% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@media (max-aspect-ratio: 819 / 546) {
    .Map {
        height: 100%;
    }
}
.InfoTitle {
    position: relative;
    margin: 20px 16px 40px 0;
}
.InfoTitle::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 30px;
    height: 3px;

    background: #eee;
    box-shadow: 1px 1px 1px 0px #111;
}
.InfoDescription {
    margin: 0;
    padding: 0 20px;
    text-align: justify;

    z-index: 1;
}

.InfoDescription-indent::before {
    content: '';
    width: 75px;
    height: 150px;
    float: left;
}
.InfoImage {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;

    z-index: -1;
}

@media (max-width: 800px) {
    .InfoImage {
        position: relative;
        box-shadow: 1px 0px 12px 3px #000a;
        z-index: 2;
    }
}
.InfoBanner {
    position: absolute;
    top: 0;
    left: 10px;

    width: 85px;

    z-index: 1;
}
.InfoPrimalSource-overlay {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 0;
    pointer-events: none;
}

.InfoPrimalSource {
    width: 200px;
    -webkit-filter: blur(7px);
            filter: blur(7px);
}
.CloseInfoPanelButton {
    position: absolute;
    top: 0;
    right: 0;
    padding: 6px;
    
    background: transparent;
    outline: none;
    border: none;
    cursor: pointer;
    color: #eee;
    text-shadow: 1px 1px 1px #111;

    z-index: 3;
}
.CloseInfoPanelButton i {
    width: 13px;
}
.ReadMoreButton {
    padding: 20px;

    text-decoration: none;
    color: #eee;
    font-weight: 500;
    text-transform: capitalize;

    margin: 14px 20px;
    padding: 8px 16px;
    border: 3px solid #eee;
    box-shadow: 1px 1px 4px 0px #111;
}
.InfoPanel-overlay {
    position: fixed;
    display: flex;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    justify-content: center;
    align-items: center;

    background: transparent;

    box-shadow: none;
    overflow: hidden;
    transition: .15s ease-in-out;
}
.InfoPanel-appear-enter-done {
    background: #0003;
    box-shadow: inset 0px 0px 120px 12px #000;
}

.InfoPanel {
    position: relative;
    width: 800px;
    height: 450px;
    box-shadow: 1px 1px 12px 3px #000;
    opacity: 1;
    overflow: hidden;
    transition: .2s ease-in-out;
}
.InfoPanel-hidden {
    opacity: 0;
    pointer-events: none;
}
.InfoPanel-content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 50%;
    height: 100%;

    background: #0009;
    box-shadow: 1px 0px 12px 3px #000a;
    -webkit-backdrop-filter: blur(7px);
            backdrop-filter: blur(7px);
}
.InfoPanel-content .InfoTitle {
    align-self: flex-end;
}
.InfoPanel-content .ReadMoreButton {
    align-self: flex-start;
}
.InfoPanel-content .InfoDescription {
    flex: 1 1;
}

@media (max-width: 800px) {
    .InfoPanel {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        box-shadow: none;

        background: #222;
    }

    .InfoPanel-content .ReadMoreButton {
        align-self: center;
    }

    .InfoPanel-content {
        flex: 1 1;
        order: 2;

        width: 100%;
    
        box-shadow: none;
        -webkit-backdrop-filter: none;
                backdrop-filter: none;

        overflow-y: scroll;
    }

    .InfoPanel-content .InfoDescription {
        padding: 0 10px;
    }
    
    .InfoPanel-content .InfoDescription-indent::before {
        width: 85px;
    }
}
.Loader-overlay {
    position: fixed;
    display: flex;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    justify-content: center;
    align-items: center;
    pointer-events: none;

    opacity: 0;
    transition: opacity .3s ease-in-out;
}
.Loader-appear-enter-done {
    opacity: 1;
}

.Loader-bar {
    margin: 4px;
    width: 8px;
    height: 30px;

    background: #eee;
    box-shadow: 1px 1px 12px 0px #111;

    animation: load1 1s infinite ease-in-out;
}
.Loader-bar {
    animation-delay: -0.16s;
}
.Loader-bar:first-child {
    animation-delay: -0.32s;
}
.Loader-bar:last-child {
    animation-delay: 0s;
}

@keyframes load1 {
    0%,
    80%,
    100% {
        height: 30px;
    }
    40% {
        height: 55px;
    }
}

.ZoomButton {
    display: flex;
    flex-direction: column;
}
.ZoomButton button {
    margin: 1px;
    padding: 6px 12px;

    cursor: pointer;
    border: none;
    outline: none;

    color: #eee;
    text-shadow: 1px 1px 1px #111;
    background: #0008;
    box-shadow: 1px 1px 4px 0px #000;

    transition: .05s ease-in-out;
}
.ZoomButton button:active {
    transform: translateY(1px);
    box-shadow: none;
}
.ZoomButton button:hover {
    background: #1118;
}
.ZoomButton button:first-child {
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}
.ZoomButton button:last-child {
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
}
.UserInterface-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    pointer-events: none;
}

.UserInterface-overlay .ZoomButton {
    position: absolute;
    right: 10px;
    bottom: 10px;
    pointer-events: initial;
}
